import Vue from 'vue';
import { Card, Form, FormItem, Input, Select, Option, Button } from 'element-ui';
import Divider from 'components/Divider';
Vue.use(Card);
Vue.use(Form);
Vue.use(FormItem);
Vue.use(Input);
Vue.use(Select);
Vue.use(Option);
Vue.use(Button);
Vue.use(Divider);
export default Vue.extend({
    data() {
        return {
            form: {
                payAccount: '',
                receiverAccount: '',
                receiverName: '',
                amount: ''
            },
            rules: {
                payAccount: [{ required: true, message: '请选择付款账户' }],
                receiverAccount: [
                    { required: true, message: '请输入收款人账户' },
                    { type: 'email', message: '账户名应为邮箱格式' }
                ],
                receiverName: [{ required: true, message: '请输入收款人姓名' }],
                amount: [
                    { required: true, message: '请输入转账金额' },
                    {
                        pattern: /^(\d+)((?:\.\d+)?)$/,
                        message: '请输入合法金额数字'
                    }
                ]
            }
        };
    },
    computed: {
        formData() {
            return this.$store.state.form.step;
        }
    },
    watch: {
        formData: {
            immediate: true,
            handler: function (val) {
                this.form = val;
            }
        }
    },
    methods: {
        onValidateForm() {
            const formRef = this.$refs.form;
            formRef.validate((valid) => {
                if (valid) {
                    this.$store.commit('form/saveStepFormData', this.form);
                    this.$router.push('/form/step-form/confirm');
                }
            });
        }
    }
});



